<template>
  <transition name="modal">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container">
          <div class="modal-header"><h3><slot name="header">default header</slot></h3></div>
          <div class="modal-body"><slot name="body">
              <ul>
                <li>default body</li>
                <li>aoth</li>
                <li>fleh</li>
              </ul>
          </slot></div>
          
          <div class="modal-footer">
            <slot name="footer">                
              <button class="modal-default-button" @click="$emit('close')">
                OK
              </button>
            </slot>
          </div>
          
        </div>
      </div>
    </div>
  </transition>
</template>

<style>
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: table;
  transition: opacity .3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 300px;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  font-family: Helvetica, Arial, sans-serif;
}

.modal-header h3 {
  margin-top: 0;
  color: #457;
}

.modal-body {
  margin: 20px 0;
}
.modal-footer {
  text-align : right;
}

.modal-default-button {
  float: right;
  margin: -10px;
}

/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
</style>

<script>
export default {
  name : 'modal'
}
</script>



